import React, { useEffect } from 'react';
import { staticPath } from '../../RouteConfig';
import { Tabs } from 'antd';
import HotelInfo from './HotelInfo'
import HotelAmenities from './HotelAmenities'
import HotelImages from './HotelImages'
import HotelSetting from './HotelSetting'
import { Switch } from 'react-router-dom'
import PropsRoute from '../../PropsRoute'
const TabPane = Tabs.TabPane;

const routes = [
  {
    path: staticPath.hotelInfo,
    title: 'Thông tin khách sạn',
    component: HotelInfo
  },
  {
    path: staticPath.hotelAmenities,
    title: 'Tiện nghi khách sạn',
    component: HotelAmenities
  },
  {
    path: staticPath.hotelImages,
    title: 'Hình ảnh khách sạn',
    component: HotelImages
  },
  {
    path: staticPath.hotelSetting,
    title: 'Cấu hình khách sạn',
    component: HotelSetting
  }
]

export default function Hotel(props) {

  useEffect(() => {
    if (props.location.pathname === staticPath.hotel) {
      props.history.push(staticPath.hotelInfo);
    }
  }, [])

  const onTabChange = (key) => {
    props.history.push(key)
  }

  const { location } = props;

  return (
    <div>
      <Switch>
        <Tabs defaultActiveKey={location.pathname} onChange={onTabChange}>
          {
            routes.map((child) => {
              return (
                <TabPane tab={child.title} key={child.path}>
                  <PropsRoute exact path={child.path} component={child.component} {...props} />
                </TabPane>
              )
            })
          }
        </Tabs>
      </Switch>
    </div>
  )
}
